.icon {
  font-family: "Material Icons", serif;
  font-style: normal;
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "liga";
  /* Make button Unhiglightable */
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                supported by Chrome, Edge, Opera and Firefox */
}
/* GENERAL */
* {
  box-sizing: border-box;
}
body {
  font-family: "Open Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  font-size: 1em;
  font-weight: 300;
  line-height: 1.5;
  position: relative;
}

/* GRID */
.row {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.navRow {
  display: flex;

  /* width: 20%; */
}
.flex-middle {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  /* justify-content: space-between; */
}
.row-middle {
  align-items: center;
}
.column {
  flex-grow: 1;
  flex-basis: 0;
  max-width: 100%;
  /* border-radius: 4px; */
}

/* Calendar */
.calendar {
  display: block;
  position: relative;
  width: 90%;
  background: "#EFF2F7";

  /* border-right: 1px solid lightgray; */
  height: auto;
  margin: 0 auto;
  border-style: none;
  border-radius: 4px;
}
.calendar .header {
  /* font-weight: 600; */
  /* font-size: 115%; */
  padding: 1.5em 0 0.5em;
  /* border-bottom: 1px solid lightgray; */
}
.icon {
  cursor: pointer;
  transition: 0.15s ease-out;
}
.icon:hover {
  transform: scale(1.25);
  transition: 0.25s ease-out;
  color: #ff5a66;
}
.icon:first-of-type {
  margin-left: 1em;
}
.icon:last-of-type {
  margin-right: 0;
}
.calendar .days {
  text-transform: uppercase;
  font-weight: 400;
  color: gray;
  font-size: 7px;
  font-family: "Roboto", sans-serif;

  /* padding: 0.75em 0; */
  /* border-bottom: 1px solid lightgray; */
}
.calendar .body .cell {
  position: relative;
  height: 3em;
  width: calc(100% / 7);
  /* border-right: 1px solid lightgray; */
  overflow: hidden;
  cursor: pointer;

  transition: 0.25s ease-out;
}

