.App {
  padding: 30px 10% 10%;
}

/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 3; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Modal Content */
.modal-content {
  background: #ffffff;
  box-shadow: 0px 6.01274px 6.01274px rgba(51, 51, 51, 0.04),
    0px 6.01274px 24.051px rgba(51, 51, 51, 0.08);
  border-radius: 12.0255px;
  margin: auto;
  padding: 20px;
  border-style: none;
  width: 710px;
  border-radius: 14.3114px;
  margin-bottom: 200px;
}

/* The Close Button */
.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #ff5a66;
  text-decoration: none;
  cursor: pointer;
}

